import { Component, ElementRef, Inject, Directive, OnInit, NgModule } from '@angular/core';

import { registerElement } from '@nativescript/angular';
import * as chartModule from 'nativescript-ui-chart';
import { ObservableArray } from '@nativescript/core';

export class SeriesComponentBase {
	protected _nativeSeries: chartModule.ChartSeries;

	get nativeSeries(): chartModule.ChartSeries {
		return this._nativeSeries;
	}
}

@Component({
	selector: 'RadCartesianChart',
	template: ``
})
export class RadCartesianChartComponent {
	protected _nativeChart: chartModule.RadCartesianChart;

	constructor(@Inject(ElementRef) private _elementRef: ElementRef) {
		this._nativeChart = _elementRef.nativeElement;
	}

	public get nativeElement(): chartModule.RadCartesianChart {
		return this._nativeChart;
	}

	public get cartesianChart(): chartModule.RadCartesianChart {
		return <chartModule.RadCartesianChart>this._nativeChart;
	}
}

@Component({
	selector: 'RadPieChart',
	template: ``
})
export class RadPieChartComponent {
	protected _nativeChart: chartModule.RadPieChart;

	constructor(@Inject(ElementRef) private _elementRef: ElementRef) {
		this._nativeChart = _elementRef.nativeElement;
	}

	public get nativeElement(): chartModule.RadPieChart {
		return this._nativeChart;
	}

	public get pieChart(): chartModule.RadPieChart {
		return <chartModule.RadCartesianChart>this._nativeChart;
	}
}

