@Directive({
	selector: '[tkCartesianSeries]'
})
export class TKCartesianSeriesDirective implements OnInit {
	constructor(@Inject(RadCartesianChartComponent) private owner: RadCartesianChartComponent, @Inject(ElementRef) private _elementRef: ElementRef) {}

	ngOnInit() {
		const series = this._elementRef.nativeElement;
		if (this.owner.cartesianChart.series) {
			this.owner.cartesianChart.series.push(series);
		} else {
			this.owner.cartesianChart.series = new ObservableArray([series]);
		}
	}
}

